type MenuCardProps = {
  title: string;
  price: string;
  image?: string;
};

export default function MenuCard({ title, price, image }: MenuCardProps) {
  return (
    <div className="border rounded-lg shadow-sm p-4 bg-white hover:shadow-md transition">
      {image && (
        <img
          src={image}
          alt={title}
          className="w-full h-32 object-cover rounded-md mb-3"
        />
      )}
      <h3 className="text-lg font-semibold text-brown-700">{title}</h3>
      <p className="text-brown-500">{price}</p>
      <button className="mt-3 w-full px-4 py-2 bg-brown-600 text-white rounded-md hover:bg-brown-700 transition">
        Add to Cart
      </button>
    </div>
  );
}
